import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Modal, Table} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge, Form} from  "react-bootstrap"
import {toast} from "react-toastify";
import axios from '../../../../config/axios'
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';
import InlineEdit from 'react-edit-inline2';
import format from "format-number";

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

export default class CreatePoLogistik extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			price       : 0,
			supplier    : '',
			idBtn       : '',
			disable     : false,
			show        : false,
			modalShow   : false,
			no_po       : '',
			no_spk      : '',
			tanggal_po  : '',
			supplier    : '',
			detail      : [],
			isLoading   : true
		}
		this.handleChange       = this.handleChange.bind(this)
		this.handleSubmit       = this.handleSubmit.bind(this)
		this.handleShow         = this.handleShow.bind(this)
		this.handleClose        = this.handleClose.bind(this);
		this.handleAddDetail    = this.handleAddDetail.bind(this);
		this.dataChangedPrice        = this.dataChangedPrice.bind(this);
		this.dataChangedSupplier        = this.dataChangedSupplier.bind(this);
	}
	
	dataChangedPrice(data, item) {
		console.log('item', item)
		const config = {
			headers: { "content-type": "multipart/form-data" }
		};
		
		let formData = new FormData()
		
		formData.append('id_request_detail'           , item.id_request_detail)
		formData.append('price'                       , data.price ? data.price : this.state.price)
		
		// console.log('formData', formData,)
		// console.log('data', data)
		// console.log('item', item)
		axios.post('purchase-order/update-item-price', formData, config)
			.then((res) => {
				let response = res.data
				if (response.status) {
					toast['success'](response.messages)
					this.loadData()
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadData()
				this.setState({
					data     : '',
					price    : data.price,
					isLoading: false
				})
			})
			.catch(function (error) {
				console.log("error");
			})
	}
	
	dataChangedSupplier(data, item) {
		// console.log('item', item)
		const config = {
			headers: { "content-type": "multipart/form-data" }
		};
		
		let formData = new FormData()
		
		formData.append('id_request_detail'           , item.id_request_detail)
		formData.append('supplier'                    , data.supplier ? data.supplier : this.state.supplier)
		
		
		// console.log('formData', formData,)
		console.log('data', data)
		console.log('item', item)
		axios.post('purchase-order/update-item-supplier', formData, config)
			.then((res) => {
				let response = res.data
				// console.log(response)
				if (response.status) {
					toast['success'](response.messages)
					this.loadData()
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadData()
				this.setState({
					data     : '',
					supplier    : data.supplier,
					isLoading: false
				})
			})
			.catch(function (error) {
				console.log("error");
			})
	}
	
	handleAddDetail(e, item){
		// console.log('add det',item)
		var data = [...this.state.data];
		data.splice(item, 1);
		// this.setState({data})
		
		this.setState({
			detail: [...this.state.detail, item],
			data
		});
	}
	
	handleClose() {
		this.setState({
			no_req  : "",
			show: false,
			modalShow: false,
			children:null
		});
	}
	
	handleShow(e) {
		// console.log('asf', item)
		// let child = []
		// item.children.map((ch, idx)=>{
		// 	child.push(ch)
		// })
		this.setState({
			// no_req  : item.no_request,
			modalShow: true,
			show: true
			// children:child
		});
	}
	
	handleChange(event){
		
		const target = event.target;
		const value = target.type === "checkbox" ? target.checked : target.value;
		const name = target.name;
		
		this.setState({
			[name]: value
		});
	}
	
	handleSubmit(){
		// console.log("project_name",this.state.project_name)
		// console.log("location",this.state.location)
		// console.log("year",this.state.year)
		// console.log("owner",this.state.owner)
		// console.log("contract_value",this.state.contract_value)
		// console.log("contract_type",this.state.contract_type)
		// console.log("start_date",this.state.start_date)
		// console.log("end_date",this.state.end_date)
		
		const config = {
			headers: { "content-type": "multipart/form-data" }
		};
		
		let formData = new FormData()
		
		formData.append('no_po', this.state.no_po )
		formData.append('no_spk', this.state.no_spk )
		formData.append('tanggal_po', this.state.tanggal_po )
		formData.append('supplier', this.state.supplier )
		formData.append('detail', JSON.stringify(this.state.detail) )
		
		axios.post('purchase-order/insert', formData, config)
			.then((res) => {
				let response = res.data
				console.log(response)
				if (response.status) {
					toast['success'](response.messages)
					this.setState({
						// no_po       : '',
						// no_spk      : '',
						// tanggal_po  : '',
						// supplier    : '',
						// detail      : [],
						isLoading       : false
					})
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
			})
			.catch(function (error) {
				toast['error']('Terjadi Kesalahan di Server')
			})
	}
	
	loadData() {
		const config = {
			headers: { "content-type": "multipart/form-data" }
		};
		
		let formData = new FormData()
		// formData.append('project_id', localStorage.getItem("id_project"))
		// formData.append('section_id', 4)
		// formData.append('user_id', 1)
		axios.post(`purchase-order/get-approval-items`,formData, config)
			.then(res => {
				const result = res.data.data
				console.log('res', res)
				if(res.status){
					this.setState({
						data        : result,
						isLoading   : false
					})
				}else{
					toast['error']('Terjadi Kesalahan Di Server')
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	componentDidMount() {
		this.loadData()
	}
	
	render() {
		
		console.log('new data', this.state.detail)
		let {data} = this.state
		let {detail} = this.state
		let {isLoading} = this.state
		var tabelDetail;
		if (this.state.modalShow === true) {
			tabelDetail = (
				<Modal
					show={this.state.modalShow}
					onHide={this.handleClose}
					size="lg"
					aria-labelledby="contained-modal-title-vcenter"
					centered
				>
					<Modal.Header closeButton>
						<Modal.Title id="contained-modal-title-vcenter">
						
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Table responsive striped bordered hover>
							<thead>
							<tr>
								<th><strong>No</strong></th>
								<th><strong>Item</strong></th>
								<th><strong>Satuan</strong></th>
								<th><strong>Jumlah</strong></th>
								<th><strong>Harga</strong></th>
								<th><strong>Supplier</strong></th>
								<th><strong>Aksi</strong></th>
							</tr>
							</thead>
							<tbody>
							{
								data.length !== 0 && data !== null ?
									data.map((itemx, idx) => {
										return (
											<tr key={idx}>
												<td>1</td>
												<td>{itemx.item_name}</td>
												<td>{itemx.uom}</td>
												<td>{itemx.quantity}</td>
												<td>
													<Badge variant="primary">
														<InlineEdit
															validate={this.customValidateText}
															activeClassName="editing"
															type="number"
															text={
																format({prefix: 'Rp ', integerSeparator: "."})
																(itemx.price == null
																	?
																	'0'
																	:
																	itemx.price)
															}
															paramName="price"
															change={(val) => this.dataChangedPrice(val, itemx)}
															style={{
																cursor:'pointer',
															}}
														/>
													</Badge>
												</td>
												<td>
													<InlineEdit
														validate={this.customValidateText}
														activeClassName="editing"
														type="text"
														text={
															itemx.supplier == null
																?
																'-'
																:
																itemx.supplier
														}
														paramName="supplier"
														change={(val) => this.dataChangedSupplier(val, itemx)}
														style={{
															cursor:'pointer',
														}}
													/>
												</td>
												<td>
													<button className="btn btn-success"
													        name={`item-`+idx}
													        size="sm"
													        disabled={this.state.disable}
													        onClick={(e) => {
													        	// console.log('e', e.target.name)
													        	this.handleAddDetail(e, itemx)
													        }}
													>
														add
													</button>
												</td>
											</tr>
										)
									})
									:
									<tr>
										<td colSpan={5}>Tidak Ada Data</td>
									</tr>
							}
							</tbody>
						</Table>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleClose}>Close</Button>
					</Modal.Footer>
				</Modal>
			)
		}else{
			tabelDetail = ""
		}
		
		return(
			<Fragment>
				<Container fixed>
					{tabelDetail}
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Create Purchase Order</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item md={12} xs={12}>
												<div className="kt-section">
													<div className="row">
														<div className="col-md-6">
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Nomor PO</Form.Label>
																<Form.Control name="no_po" onChange={this.handleChange} type="text" placeholder="" />
															</Form.Group>
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Nomor SPK</Form.Label>
																<Form.Control name="no_spk" onChange={this.handleChange} type="text" placeholder="" />
															</Form.Group>
														</div>
														<div className="col-md-6">
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Supplier</Form.Label>
																<Form.Control name="supplier" onChange={this.handleChange} type="text" placeholder="" />
															</Form.Group>
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Tanggal</Form.Label>
																<Form.Control name="tanggal_po" onChange={this.handleChange} type="date" placeholder="" />
															</Form.Group>
														</div>
														<div className="col-md-12">
															<Button variant="success" type="submit" style={{float:"right"}}
															        onClick={(e) => this.handleShow(e)}
															>
																Add Detail
															</Button>
														</div>
													</div>
													
													<div className="kt-space-20" />
													
													<div className="row">
														<div className="col-md-12">
															<div className="col-md-12" style={{marginTop:20}}>
																<Table responsive striped bordered hover>
																	<thead>
																	<tr>
																		<th><strong>No</strong></th>
																		<th><strong>Nama Material</strong></th>
																		<th><strong>Satuan</strong></th>
																		<th><strong>Jumlah</strong></th>
																		<th><strong>Harga</strong></th>
																		<th><strong>Supplier</strong></th>
																		<th><strong>Action</strong></th>
																	</tr>
																	</thead>
																	<tbody>
																	{
																		detail.length !== 0 && detail !== null ?
																			detail.map((item, index)=>{
																				return(
																					<tr key={index}>
																						<td>1</td>
																						<td>{item.item_name}</td>
																						<td>{item.uom}</td>
																						<td>{item.quantity}</td>
																						<td>
																							<Badge variant="primary">
																								{format({prefix: 'Rp ', integerSeparator: "."})(item.price)}
																							</Badge>
																						</td>
																						<td>{item.supplier}</td>
																						<td>
																							<Button variant="outline-danger" size="sm" style={{marginRight:5}}><DeleteForeverOutlinedIcon/></Button>
																						</td>
																					</tr>
																				)
																			})
																			:
																			<tr>
																				<td colSpan={5}>Tidak ada data</td>
																			</tr>
																	}
																	</tbody>
																</Table>
															</div>
														</div>
													</div>
													
													<div className="row">
														<div className="col-md-12">
															<Button variant="primary" type="submit" style={{float:"right"}}
															        onClick={this.handleSubmit}
															>
																Save
															</Button>
														</div>
													</div>
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}